import React from 'react';
import classNames from 'classnames';

export default function CheckSwitch({
  checked,
  setChecked,
}: {
  checked: boolean;
  setChecked: (value: boolean) => void;
}) {
  const wrapperClass = classNames('check-switch-wrapper', { checked });
  return (
    <div className={wrapperClass} onClick={() => setChecked(!checked)}>
      <div className="check-switch-thumb"></div>
    </div>
  );
}
