import classNames from 'classnames';
import React, { useCallback, useEffect, useRef } from 'react';
import decreateIcon from '../../assets/main-window/decreate.svg';
import increateIcon from '../../assets/main-window/increate.svg';

type NumericInputProps = {
  className?: string;
  min?: number;
  max?: number;
  value: number;
  exitEdit?: () => void;
  setValue: React.Dispatch<React.SetStateAction<number>>;
};

function limitValue(value: number, min: number, max: number) {
  let correctedValue = Math.max(value, min);
  correctedValue = Math.min(correctedValue, max);
  return correctedValue;
}

const NumericInput: React.FC<NumericInputProps> = ({
  className,
  min,
  max,
  value,
  exitEdit,
  setValue,
}) => {
  const inputRef = useRef<HTMLInputElement>(null);
  useEffect(() => {
    const inputElement = inputRef.current;
    if (inputElement && exitEdit) {
      inputElement.addEventListener('blur', exitEdit);

      return () => inputElement.removeEventListener('blur', exitEdit);
    }
  }, [exitEdit]);
  const handleValueChanged = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      const inputValue = Number.parseInt(e.target.value);
      setValue(limitValue(inputValue, min!, max!));
    },
    [max, min, setValue]
  );
  const changeValue = useCallback(
    (reducer: (oldValue: number) => number) =>
      setValue((oldValue) => limitValue(reducer(oldValue), min!, max!)),
    [max, min, setValue]
  );
  const decreaseValue = useCallback(() => changeValue((oldValue) => oldValue - 1), [changeValue]);
  const increaseValue = useCallback(() => changeValue((oldValue) => oldValue + 1), [changeValue]);

  const wrapperClass = classNames('numeric-input-wrapper', className);
  const decreaseButtonClass = classNames('button-change-value', { disable: value <= min! });
  const increaseButtonClass = classNames('button-change-value', { disable: value >= max! });

  return (
    <div className={wrapperClass}>
      <button className={decreaseButtonClass} onClick={decreaseValue}>
        <img src={decreateIcon} alt="decreate" width="20px" height="20px" />
      </button>
      <input
        ref={inputRef}
        className="input-number"
        type="text"
        value={Number.isNaN(value) ? '' : value}
        onChange={handleValueChanged}
      />
      <button className={increaseButtonClass} onClick={increaseValue}>
        <img src={increateIcon} alt="increate" width="20px" height="20px" />
      </button>
    </div>
  );
};

NumericInput.defaultProps = {
  min: Number.MIN_SAFE_INTEGER,
  max: Number.MAX_SAFE_INTEGER,
};

export default NumericInput;
