import { LanguageCode } from '../../i18n';
import {
  Dispose,
  EventHandler,
  importJavaFunc,
  importJavaPromise,
  importJavaPromiseNonJson,
  subscribeJavaEvent,
} from '../../java-interop';

// ***************************************************************************
// Java Event
// ***************************************************************************

export type RecentEventMessage = {
  unreadCount: number;
};

export type SyncStatusType = 'failed' | 'paused' | 'syncing' | 'synced';

export type SyncStatusMessage = {
  isPaused: boolean;
  type: SyncStatusType;
  details: string;
};

function subscribeMainWindowEvent(channel: 'update-sandbox', handler: EventHandler<void>): Dispose;
function subscribeMainWindowEvent(
  channel: 'update-recent-event',
  handler: EventHandler<RecentEventMessage>
): Dispose;
function subscribeMainWindowEvent(
  channel: 'update-sync-status',
  handler: EventHandler<SyncStatusMessage>
): Dispose;
function subscribeMainWindowEvent(
  channel: 'goto-settings-tab',
  handler: EventHandler<void>
): Dispose;
function subscribeMainWindowEvent(channel: any, handler: any) {
  return subscribeJavaEvent(channel, handler);
}

export { subscribeMainWindowEvent };

export const executeCallbackEventsAsync =
  importJavaPromiseNonJson<() => void>('executeCallbackEvents');

