// API: Search
export type SearchResult = {
  isDir: boolean;
  name: string;
  parentDir: string;
  fullPath: string;
};

export const getSearchResultsAsync =
  importJavaPromise<(searchText: string, isGetAll: boolean) => SearchResult[]>('getSearchResults');

const getSearchHistoryInternal = importJavaFunc<() => string>('getSearchHistory');
export function getSearchHistory(): string[] {
  return JSON.parse(getSearchHistoryInternal());
}

const setSearchHistoryInternal = importJavaFunc<(json: string) => void>('setSearchHistory');
export function setSearchHistory(history: string[]) {
  setSearchHistoryInternal(JSON.stringify(history));
}

