import { useState, useEffect, useMemo } from 'react';
import { ComboBoxItem } from '../../components/items-control';
import { useI18n } from '../../i18n';
import {
  RecentEventItem,
  getSyncFolderItemsAsync,
  subscribeMainWindowEvent,
  getRecentEventsAsync,
} from '../../java-api/main-window';

function useComboItemAllSandboxes(): ComboBoxItem {
  const { $t } = useI18n();
  return useMemo(
    () => ({
      value: -1,
      label: $t('recent.comboItemText.allSandboxes'),
    }),
    [$t]
  );
}

export default function useRecentViewModel() {
  const comboItemAllSandboxes = useComboItemAllSandboxes();
  const [selectedItem, setSelecetdItem] = useState<ComboBoxItem>(comboItemAllSandboxes);
  const [syncFolderItems, setSyncFolderItems] = useState<ComboBoxItem[]>([]);
  const [recentEventItems, setRecentEventItems] = useState<RecentEventItem[]>([]);
  useEffect(() => {
    const fetchSyncFolderItems = async () => {
      const folderItems = await getSyncFolderItemsAsync();
      setSyncFolderItems(folderItems.map((item) => ({ value: item.id, label: item.name })));
    };
    const updateSandboxEventToken = subscribeMainWindowEvent('update-sandbox', () =>
      fetchSyncFolderItems()
    );
    fetchSyncFolderItems();

    return () => updateSandboxEventToken();
  }, []);

  useEffect(() => {
    const fetchRecentEventItems = async () => {
      const recentEventItems = await getRecentEventsAsync(selectedItem.value as number);
      setRecentEventItems(recentEventItems);
    };
    const dispose = subscribeMainWindowEvent('update-recent-event', () => fetchRecentEventItems());
    fetchRecentEventItems();

    return () => dispose();
  }, [selectedItem.value]);

  return {
    comboItemAllSandboxes,
    syncFolderItems,
    selectedItem,
    recentEventItems,
    setSelecetdItem,
  };
}
