import React, { ReactNode } from 'react';
import { ScrollViewer } from '../../components/layout-container';
import CheckSwitch from '../../components/input/check-switch';
import rightArrowIcon from '../../assets/right-arrow.svg';
import leftArrowIcon from '../../assets/left-arrow.svg';

type SettingsButtonProps = {
  text: string;
  onClick: () => void;
};

type SettingItemSwitchProps = {
  title: string;
  decription?: string;
  checked: boolean;
  setChecked: (value: boolean) => void;
};

type SettingsViewTemplateProps = {
  header: string;
  content: ReactNode;
  footer: ReactNode;
  onHeaderClicked?: () => void;
};

export function SettingsHeaderButton({ text, onClick }: SettingsButtonProps) {
  return (
    <button className="settings-button header" onClick={onClick}>
      <img className="button-icon" src={leftArrowIcon} alt="right-arrow" height="8px" />
      <span className="text button-text">{text}</span>
    </button>
  );
}

export function SettingsFooterButton({ text, onClick }: SettingsButtonProps) {
  return (
    <button className="settings-button footer" onClick={onClick}>
      <span className="text button-text">{text}</span>
      <img className="button-icon" src={rightArrowIcon} alt="right-arrow" height="8px" />
    </button>
  );
}

export function SettingItemSwitch({
  title,
  decription,
  checked,
  setChecked,
}: SettingItemSwitchProps) {
  return (
    <div className="setting-item-switch-wrapper">
      <div className="setting-item-content">
        <span className="text setting-item-title">{title}</span>
        {decription ? <span className="text setting-item-description">{decription}</span> : null}
      </div>
      <CheckSwitch checked={checked} setChecked={setChecked} />
    </div>
  );
}

export function SettingsViewTemplate({
  header,
  content,
  footer,
  onHeaderClicked,
}: SettingsViewTemplateProps) {
  return (
    <ScrollViewer>
      <div className="setting-view-template-wrapper">
        <div className="setting-view-template-region">
          <div className="header-region">
            {onHeaderClicked ? (
              <SettingsHeaderButton text={header} onClick={onHeaderClicked} />
            ) : (
              <span className="text header">{header}</span>
            )}
          </div>
          <div className="content-region">{content}</div>
          <div className="footer-region">{footer}</div>
        </div>
      </div>
    </ScrollViewer>
  );
}
