import React from 'react';
import classNames from 'classnames';

type SliderBarProps = {
  className?: string;
  min?: number;
  max?: number;
  value: number;
  setValue: (value: number) => void;
};

const SliderBar: React.FC<SliderBarProps> = ({ className, min, max, value, setValue }) => {
  let percentage = (100 * (value - min!)) / (max! - min!);
  percentage = Math.max(percentage, 0);
  percentage = Math.min(percentage, 100);
  const wrapperClass = classNames('slider-bar-wrapper', className);

  return (
    <div className={wrapperClass}>
      <input
        className="slider-bar"
        type="range"
        min={min}
        max={max}
        value={value}
        onChange={(e) => setValue(Number.parseFloat(e.target.value))}
      />
      <div className="slider-progress" style={{ width: `calc(${percentage}% + 2px)` }} />
    </div>
  );
};

SliderBar.defaultProps = {
  min: 0,
  max: 100,
};

export default SliderBar;
