import classNames from 'classnames';
import React, { useRef, useState } from 'react';
import ContextMenu, { MenuItem } from '../items-control/context-menu';
import { Tooltip } from '../tooltip';

type DropdownButtonProps = {
  icon: string;
  tooltip: string,
  items: MenuItem[];
  onActiveChanged?: (value: boolean) => void;
};

export default function DropdownButton({ icon, tooltip, items, onActiveChanged }: DropdownButtonProps) {
  const buttonRef = useRef<HTMLButtonElement>(null);
  const [active, setActive] = useState(false);
  const buttonClass = classNames('dropdown-button', { active });

  return (
    <ContextMenu
      items={items}
      onActiveChanged={(value) => {
        if (onActiveChanged) {
          onActiveChanged(value);
        }
        setActive(value);
      }}
      renderTarget={(open) => {
        return (
          <button
            ref={buttonRef}
            className={buttonClass}
            onClick={(e) => {
              e.stopPropagation();
              if (buttonRef.current) {
                const buttonRect = buttonRef.current.getBoundingClientRect();
                open({
                  x: buttonRect.left,
                  y: buttonRect.top,
                  width: buttonRef.current.offsetWidth,
                  height: buttonRef.current.offsetHeight,
                });
              }
            }}
          >
            <Tooltip title={tooltip}>
              <img className="dropdown-icon" src={icon} alt="dropdown-icon" />
            </Tooltip>
          </button>
        );
      }}
    />
  );
}
