import classNames from 'classnames';
import React, { ReactElement } from 'react';
import { Tooltip } from '../tooltip';

export type TabItemHeader = {
  icon: ReactElement | string;
  text: string;
  changeCount?: number;
};

type HamburgTabHeaderItemProps<TKey extends string> = {
  isExpanded: boolean;
  header: TabItemHeader;
  itemKey: TKey;
  selected: boolean;
  setSelectedKey: (value: TKey) => void;
  onClick?: () => void;
};

export function HamburgTabHeaderItem<TKey extends string>({
  isExpanded,
  header,
  itemKey,
  selected,
  setSelectedKey,
  onClick,
}: HamburgTabHeaderItemProps<TKey>) {
  const headerItemClass = classNames('hamburg-tab-header-item', {
    selected,
  });
  return (
    <Tooltip title={header.text} placement='right'>
      <div
        className={headerItemClass}
        onClick={() => {
          if (onClick) {
            onClick();
          }
          setSelectedKey(itemKey);
        }}
      >
        {typeof header.icon === 'string' ? (
          <img className="hamburg-tab-header-icon" src={header.icon} alt="header-icon" />
        ) : (
          React.cloneElement(header.icon, { className: 'hamburg-tab-header-icon' })
        )}
        {isExpanded ? (
          <>
            <span className="hamburg-tab-header-title text ellipsis">{header.text}</span>
            {header.changeCount ? (
              <div className="change-count-badge">
                {header.changeCount < 100 ? header.changeCount : '99+'}
              </div>
            ) : null}
          </>
        ) : header.changeCount ? (
          <div className="change-count-dot"></div>
        ) : null}
      </div>
    </Tooltip>
  );
}
