import { useState, useRef, useEffect, useCallback } from 'react';
import { useThrottle } from '../../hooks/use-throttle';
import { getBookmarkItemsAsync, BookmarkItem } from '../../java-api/main-window';

async function fetchBookmarkItemsAsync() {
  const items = await getBookmarkItemsAsync();
  items.sort((a, b) => {
    if ((!a.isDir && !b.isDir) || (a.isDir && b.isDir)) {
      return a.name.localeCompare(b.name);
    } else {
      return a.isDir ? -1 : 1;
    }
  });

  return items;
}

export default function useBookmarksViewModel(selected: boolean) {
  const [isSearchMode, setIsSearchMode] = useState(false);
  const allBookmarkItemsRef = useRef<BookmarkItem[]>();
  const searchBoxRef = useRef<HTMLInputElement>(null);
  const [bookmarkItems, setBookmarkItems] = useState<BookmarkItem[]>([]);
  const fetchBookmarkItems = async () => {
    const newItems = await fetchBookmarkItemsAsync();
    setBookmarkItems(newItems);
    allBookmarkItemsRef.current = newItems;
    if (searchBoxRef.current) {
      searchBoxRef.current.value = '';
    }
  };
  useEffect(() => {
    if (selected) {
      fetchBookmarkItems();
    }
  }, [selected]);
  useEffect(() => {
    if (!isSearchMode && searchBoxRef.current && allBookmarkItemsRef.current) {
      setBookmarkItems(allBookmarkItemsRef.current);
      searchBoxRef.current.value = '';
    }
  }, [isSearchMode]);
  const filterBookmarkItems = useCallback((value: string) => {
    if (!allBookmarkItemsRef.current) return;

    if (value) {
      const lowerValue = value.toLowerCase();
      const filteredItems = allBookmarkItemsRef.current.filter((item) =>
        item.name.toLowerCase().includes(lowerValue)
      );
      setBookmarkItems(filteredItems);
    } else {
      setBookmarkItems(allBookmarkItemsRef.current);
    }
  }, []);
  const filterBookmarkItemsThrottle = useThrottle(filterBookmarkItems, 300);

  return {
    searchBoxRef,
    isSearchMode,
    bookmarkItems,
    setIsSearchMode,
    filterBookmarkItemsThrottle,
    fetchBookmarkItems,
  };
}
