import React, { useCallback, useEffect } from 'react';
import { Button } from '../../components/button';
import { Input } from '../../components/input';
import { ComboBox, useNavigator } from '../../components/items-control';
import { useAsyncConfigureValue } from '../../hooks/use-configure-value';
import { useI18n } from '../../i18n';
import { getLANSyncChangeAsync, setLANSyncChangeAsync } from '../../java-api/main-window';
import useProxySettingsModel from '../../models/launch-window/proxy-settings-model';
import { SettingsViewName, SWITCH_NAME_SETTINGS } from './settings-view';
import { SettingItemSwitch, SettingsViewTemplate } from './settings-view-template';

export default function SettingsNetworkView() {
  const { $t } = useI18n();
  const {
    proxyTypes,
    selectedProxyType,
    disabledProxy,
    hostField,
    portField,
    usernameField,
    passwordField,
    setSelectedProxyType,
    submitProxySettings,
    resetProxyAsync,
  } = useProxySettingsModel();
  const lanSync = useAsyncConfigureValue(getLANSyncChangeAsync, setLANSyncChangeAsync, false);
  useEffect(() => {
    lanSync.reset(true);
  }, [lanSync]);
  const settingsNavigator = useNavigator<SettingsViewName>(SWITCH_NAME_SETTINGS);
  const save = useCallback(async () => {
    await lanSync.apply();
    await submitProxySettings(() => {
      settingsNavigator('general');
    });
  }, [lanSync, settingsNavigator, submitProxySettings]);
  const cancel = useCallback(async () => {
    lanSync.reset();
    await resetProxyAsync();
    settingsNavigator('general');
  }, [lanSync, resetProxyAsync, settingsNavigator]);

  return (
    <SettingsViewTemplate
      header={$t('settings.network.title')}
      onHeaderClicked={cancel}
      content={
        <div className="settings-view content-network">
          <SettingItemSwitch
            title={$t('settings.network.enableLANSync')}
            checked={lanSync.value}
            setChecked={(v) => (lanSync.value = v)}
          />
          <div className="network-proxy-type-item">
            <span className="text subtitle label-proxy-type">
              {$t('settings.network.proxy.type')}
            </span>
            <ComboBox
              itemsSource={proxyTypes}
              selectedItem={selectedProxyType}
              setSelectedItem={(item) => setSelectedProxyType(item)}
            />
          </div>
          <div className="network-proxy-info">
            <Input
              className="input-proxy-host"
              placeholder={$t('settings.network.proxy.host')}
              value={hostField.value}
              onChange={(e) => hostField.setOnly(e.target.value)}
              errorText={hostField.error}
              disabled={disabledProxy}
            />
            <Input
              className="input-proxy-port"
              placeholder={$t('settings.network.proxy.port')}
              value={portField.value}
              onChange={(e) => portField.setOnly(e.target.value)}
              errorText={portField.error}
              disabled={disabledProxy}
            />
            <span className="text subtitle label-proxy-authentication">
              {$t('settings.network.proxy.authentication')}
            </span>
            <Input
              className="input-proxy-account"
              placeholder={$t('settings.network.proxy.account')}
              value={usernameField.value}
              onChange={(e) => usernameField.setOnly(e.target.value)}
              errorText={usernameField.error}
              disabled={disabledProxy}
            />
            <Input
              className="input-proxy-password"
              placeholder={$t('settings.network.proxy.password')}
              value={passwordField.value}
              onChange={(e) => passwordField.setOnly(e.target.value)}
              errorText={passwordField.error}
              disabled={disabledProxy}
            />
          </div>
        </div>
      }
      footer={
        <div className="settings-view footer-network">
          <Button onClick={cancel}>{$t('button.cancel')}</Button>
          <Button buttonType="primary" onClick={save}>
            {$t('button.save')}
          </Button>
        </div>
      }
    />
  );
}
