import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { SearchBox } from '../../components/input';
import { CaseItem, Switch } from '../../components/items-control';
import { ScrollViewer } from '../../components/layout-container';
import SyncFolderSearchResultView from './sync-folder-search-result-view';
import { useI18n } from '../../i18n';
import { DropdownButton } from '../../components/button';
import ContextMenu, { MenuItem } from '../../components/items-control/context-menu';
import { Rect } from '../../components/common-types';
import classNames from 'classnames';

// import icons
import stateLockedIcon from '../../assets/main-window/sync_state_locked.svg';
import statePlaceholderReadonlyIcon from '../../assets/main-window/sync_state_placeholder_readonly.svg';
import statePlacehoderIcon from '../../assets/main-window/sync_state_placeholder.svg';
import stateReadonlyIcon from '../../assets/main-window/sync_state_readonly.svg';
import stateSyncingIcon from '../../assets/main-window/sync_state_syncing.svg';
import stateUpToDateIcon from '../../assets/main-window/sync_state_uptodate.svg';
import itemButtonMoreIcon from '../../assets/main-window/item_button_more.svg';
import itemButtonUnpinIcon from '../../assets/main-window/item_button_unpin.svg';
import itemButtonPinIcon from '../../assets/main-window/item_button_pin.svg';
import folderPrivateIcon from '../../assets/main-window/folder_private.svg';
import folderPublicIcon from '../../assets/main-window/folder_public.svg';
import menuNewIcon from '../../assets/main-window/menu_new.svg';
import menuShareIcon from '../../assets/main-window/menu_share.svg';
import menuHistoryIcon from '../../assets/main-window/menu_history.svg';
import menuExceptionIcon from '../../assets/main-window/menu_exception.svg';
import {
  bindSyncFolder,
  copySyncInviteLink,
  migrateSyncFolder,
  openACLDialog,
  openBasicInfoDialog,
  openFileHistoryWizard,
  openSandbox,
  openPrivateSyncFolderWizard,
  openPublicSyncFolderWizard,
  openRestoreFilesWizard,
  openShareViaEmailWizard,
  openShareViaLinkWizard,
  openSyncToOtherDevicesWizard,
  SyncFolderItem,
  SyncFolderState,
  unbindSyncFolder,
  getSyncFolderStateAsync,
  subscribeMainWindowEvent,
  openSyncErrorsWindow,
} from '../../java-api/main-window';
import useSyncFolderViewModel, {
  MainContentViewName,
  SWITCH_NAME_SYNC_FOLDER_CONTENT,
  useSyncFolderMainContentModel,
} from '../../models/main-window/sync-folder-view-model';
import { useIsFileProviderEnabled } from '../../java-api';
import { Tooltip } from '../../components/tooltip';

function getSyncStateIcon(syncState: SyncFolderState) {
  switch (syncState) {
    case 'locked':
      return stateLockedIcon;
    case 'placeholder':
      return statePlacehoderIcon;
    case 'readonly':
      return stateReadonlyIcon;
    case 'syncing':
      return stateSyncingIcon;
    case 'up-to-date':
      return stateUpToDateIcon;
    case 'placeholder-readonly':
      return statePlaceholderReadonlyIcon;
    default:
      return null;
  }
}

type FolderItemIconProps = {
  item: SyncFolderItem;
  state: SyncFolderState;
};

type FolderListBoxItemProps = {
  item: SyncFolderItem;
  isPinned: boolean;
  pinOrUnpin: () => void;
};

type PinnedFolderListBoxItemProps = {
  item: SyncFolderItem;
  unpin: () => void;
};

function FolderItemIcon({ item, state }: FolderItemIconProps) {
  const stateIcon = getSyncStateIcon(state);
  return (
    <div className="folder-item-icon">
      <img
        className="folder-icon"
        src={item.isPrivate ? folderPrivateIcon : folderPublicIcon}
        alt="folder-icon"
      />
      {stateIcon ? (
        <img className="folder-state-icon" src={stateIcon} alt="folder-state-icon" />
      ) : null}
    </div>
  );
}

function FolderListBoxItem({ item, isPinned, pinOrUnpin }: FolderListBoxItemProps) {
  const { $t } = useI18n();
  const isFileProviderEnabled = useIsFileProviderEnabled();
  const [activeMenu, setActiveMenu] = useState(false);
  const [syncFolderState, setSyncFolderState] = useState<SyncFolderState>('unknown');
  useEffect(() => {
    const fetch = async () => setSyncFolderState(await getSyncFolderStateAsync(item.id));

    const dispose = subscribeMainWindowEvent('update-sync-status', () => fetch());
    fetch();

    return () => dispose();
  }, [item.id]);
  const folderItemMenuItems = useMemo<MenuItem[]>(() => {
    const menuItems: MenuItem[] = [];
    const isSyncToDisk = item.isSyncedToDisk;
    const isPlaceholder = item.isPlaceholder;

    if (isFileProviderEnabled) {
      if (!isPlaceholder) {
        menuItems.push({
          text: $t('syncFolder.menu.syncAsPlaceholder'),
          onClick: async () => await bindSyncFolder(item.id),
        });
      }
    } else {
      if (!isSyncToDisk) {
        menuItems.push({
          text: $t('syncFolder.menu.syncToDisk'),
          onClick: async () => await bindSyncFolder(item.id),
        });
      }
    }

    menuItems.push({
      text: $t('syncFolder.menu.accessViaWeb'),
      onClick: () => openSandbox(item.id, true),
    });

    if (item.canChangeRight) {
      menuItems.push('split-line');
      menuItems.push({
        text: $t('syncFolder.menu.basicInfo'),
        onClick: async () => await openBasicInfoDialog(item.id),
      });
      menuItems.push({
        text: $t('syncFolder.menu.shareConfigure'),
        onClick: async () => await openACLDialog(item.id),
      });
      menuItems.push({
        text: $t('syncFolder.menu.copySyncInvitationLink'),
        onClick: async () => await copySyncInviteLink(item.id),
      });
    }

    if (isFileProviderEnabled) {
      if (isPlaceholder) {
        menuItems.push({
          text: $t('syncFolder.menu.stopSyncPlaceholder'),
          onClick: async () => await unbindSyncFolder(item.id),
        });
      }
    } else {
      if (isSyncToDisk) {
        menuItems.push('split-line');

        menuItems.push({
          text: $t('syncFolder.menu.changeSyncPath'),
          onClick: async () => await migrateSyncFolder(item.id),
        });

        menuItems.push({
          text: $t('syncFolder.menu.stopSync'),
          onClick: async () => await unbindSyncFolder(item.id),
        });
      }
    }

    return menuItems;
  }, [
    $t,
    item.canChangeRight,
    item.id,
    item.isSyncedToDisk,
    item.isPlaceholder,
    isFileProviderEnabled,
  ]);
  const wrapperClass = classNames('folder-list-item-wrapper', { active: activeMenu });

  return (
    <div
      className={wrapperClass}
      onClick={(e) => {
        if (e.currentTarget.contains(e.target as Node)) {
          openSandbox(item.id, false);
        }
      }}
    >
      <FolderItemIcon item={item} state={syncFolderState} />
      <span className="folder-name-text text ellipsis link">{item.name}</span>
      <span className="folder-permissions-text">{item.permissions}</span>
      <div className="folder-item-buttons">
        <Tooltip title={isPinned ? $t('folderItem.menu.unpin.tooltip') : $t('folderItem.menu.pin.tooltip')}>
          <button
            className="folder-item-button"
            onClick={(e) => {
              e.stopPropagation();
              pinOrUnpin();
            }}
          >
            <img
              src={isPinned ? itemButtonUnpinIcon : itemButtonPinIcon}
              alt={isPinned ? 'folder-unpin' : 'folder-pin'}
              width="20px"
              height="20px"
            />
          </button>
        </Tooltip>
        <DropdownButton
          onActiveChanged={setActiveMenu}
          icon={itemButtonMoreIcon}
          tooltip={$t('folderItem.menu.more.tooltip')}
          items={folderItemMenuItems}
        />
      </div>
    </div>
  );
}

function PinnedFolderListBoxItem({ item, unpin }: PinnedFolderListBoxItemProps) {
  const { $t } = useI18n();
  const [syncFolderState, setSyncFolderState] = useState<SyncFolderState>('unknown');
  useEffect(() => {
    const fetch = async () => setSyncFolderState(await getSyncFolderStateAsync(item.id));

    const dispose = subscribeMainWindowEvent('update-sync-status', () => fetch());
    fetch();

    return () => dispose();
  }, [item.id]);
  const renderContent = useCallback(
    (open: (rect: Rect) => void) => {
      return (
        <button
          className="pinned-folder-list-item-wrapper"
          onContextMenu={(e) => open({ x: e.clientX, y: e.clientY, width: 0, height: 0 })}
          onClick={(e) => {
            if (e.currentTarget.contains(e.target as Node)) {
              openSandbox(item.id, false);
            }
          }}
        >
          <FolderItemIcon item={item} state={syncFolderState} />
          <span className="text ellipsis folder-name-text">{item.name}</span>
        </button>
      );
    },
    [item, syncFolderState]
  );
  const pinnedFolderItemMenuItems = useMemo(
    () => [{ text: $t('syncFolder.menu.unpin'), onClick: unpin }],
    [$t, unpin]
  );

  return <ContextMenu items={pinnedFolderItemMenuItems} renderTarget={renderContent} />;
}

function MainContent() {
  const { $t } = useI18n();
  const { syncFolderItems, pinnedSandboxIdList, setIsPinned } = useSyncFolderMainContentModel();

  return (
    <ScrollViewer>
      <div className="sync-folder-group-region">
        {pinnedSandboxIdList.length ? (
          <>
            <span className="text tip-secondary label-folder-group-name">
              {$t('syncFolder.label.pinnedFolder')}
            </span>
            <div className="sync-folder-group-pinned">
              {pinnedSandboxIdList
                .map((pinnedId) => syncFolderItems.find((item) => item.id === pinnedId))
                .filter((item) => !!item)
                .map((item) => (
                  <PinnedFolderListBoxItem
                    key={item!.id}
                    item={item!}
                    unpin={() => setIsPinned(item!.id, false)}
                  />
                ))}
            </div>
          </>
        ) : null}
        <span className="text tip-secondary label-folder-group-name">
          {$t('syncFolder.label.folder')}
        </span>
        <div className="sync-folder-group-unpinned">
          {syncFolderItems.map((item) => {
            const isPinned = !!pinnedSandboxIdList.find((pinnedId) => pinnedId === item.id);
            return (
              <FolderListBoxItem
                key={item.id}
                item={item}
                isPinned={isPinned}
                pinOrUnpin={() => setIsPinned(item.id, !isPinned)}
              />
            );
          })}
        </div>
      </div>
    </ScrollViewer>
  );
}

export default function SyncFolderView() {
  const { $t } = useI18n();
  const newMenuItems = useMemo(
    () => [
      {
        text: $t('syncFolder.menu.newSyncFolder'),
        onClick: async () => await openPrivateSyncFolderWizard(),
      },
      {
        text: $t('syncFolder.menu.inviteFriendsToSync'),
        onClick: async () => await openPublicSyncFolderWizard(),
      },
    ],
    [$t]
  );
  const shareMenuItems = useMemo(
    () => [
      { text: $t('syncFolder.menu.getShareLink'), onClick: async () => await openShareViaLinkWizard() },
      { text: $t('syncFolder.menu.sendByEmail'), onClick: async () => await openShareViaEmailWizard() },
      { text: $t('syncFolder.menu.syncWithOtherDevices'), onClick: async () => await openSyncToOtherDevicesWizard() },
    ],
    [$t]
  );
  const exceptionMenuItems = useMemo(
    () => [
      {
        text: $t('syncFolder.menu.viewSyncErrors'),
        onClick: async (event?: React.MouseEvent) => {
          // If ALT key is pressed while clicking this menu item,
          // we should include the deleted sync errors in the SyncErrorsWindow.
          var includedDeleted = event?.altKey || false;
          await openSyncErrorsWindow(includedDeleted);
        }
      },
    ],
    [$t]
  );
  const historyMenuItems = useMemo(
    () => [
      { text: $t('syncFolder.menu.viewFileHistory'), onClick: async () => await openFileHistoryWizard() },
      { text: $t('syncFolder.menu.restoreDeletedFiles'), onClick: async () => await openRestoreFilesWizard() },
    ],
    [$t]
  );
  const subViews = useMemo<CaseItem<MainContentViewName>[]>(
    () => [
      {
        label: 'normal',
        renderer: () => <MainContent />,
      },
      {
        label: 'search-result',
        renderer: (props) => <SyncFolderSearchResultView {...props} />,
      },
    ],
    []
  );

  const dropdownButtons = useMemo(() => {
    const buttons: JSX.Element[] = [];
    buttons.push(<DropdownButton icon={menuNewIcon} tooltip={$t('syncFolderView.menu.new.tooltip')} items={newMenuItems} />);
    buttons.push(<DropdownButton icon={menuShareIcon} tooltip={$t('syncFolderView.menu.share.tooltip')} items={shareMenuItems} />);
    buttons.push(<DropdownButton icon={menuExceptionIcon} tooltip={$t('syncFolderView.menu.exception.tooltip')} items={exceptionMenuItems} />);
    buttons.push(<DropdownButton icon={menuHistoryIcon} tooltip={$t('syncFolderView.menu.history.tooltip')} items={historyMenuItems} />);
    return buttons;
  }, [$t, newMenuItems, shareMenuItems, exceptionMenuItems, historyMenuItems]);
  const { searchBoxRef, isSearchMode, setIsSearchMode, onSearchTextChangedThrottle } =
    useSyncFolderViewModel();

  return (
    <div className="sync-folder-view-wrapper">
      <div className="search-box-region">
        <SearchBox
          ref={searchBoxRef}
          placeholder={$t('syncFolder.search.placeholder')}
          onChange={(e) => {
            onSearchTextChangedThrottle(e.target.value);
          }}
          isEditing={isSearchMode}
          setIsEditing={setIsSearchMode}
          addon={<div className="search-box-addon-list">{dropdownButtons}</div>}
        />
      </div>
      <div className="folder-view-region">
        <Switch name={SWITCH_NAME_SYNC_FOLDER_CONTENT} cases={subViews} />
      </div>
    </div>
  );
}
