import { useState, useCallback, useEffect } from 'react';
import { useNavigator } from '../../components/items-control';
import { useConfigureValue } from '../../hooks/use-configure-value';
import {
  getCacheMaxUsagePercentage,
  setCacheMaxUsagePercentage,
  getCacheRemainDays,
  setCacheRemainDays,
  getCacheCurrentUsageSizeAsync,
  getCacheTotalSizeAsync,
} from '../../java-api/main-window';
import { SettingsViewName, SWITCH_NAME_SETTINGS } from '../../views/main-window/settings-view';

export default function useSettingsStorageViewModel() {
  const settingsNavigator = useNavigator<SettingsViewName>(SWITCH_NAME_SETTINGS);
  const [currentUsageSize, setCurrentUsageSize] = useState(0);
  const [diskTotalSize, setDiskTotalSize] = useState(0);
  const maxUsagePercentage = useConfigureValue<number>(
    getCacheMaxUsagePercentage,
    setCacheMaxUsagePercentage
  );
  const remainDays = useConfigureValue<number>(getCacheRemainDays, setCacheRemainDays);
  const fetchSizeInfoAsync = useCallback(async () => {
    setCurrentUsageSize(await getCacheCurrentUsageSizeAsync());
    setDiskTotalSize(await getCacheTotalSizeAsync());
  }, []);

  const save = useCallback(() => {
    maxUsagePercentage.apply();
    remainDays.apply();

    settingsNavigator('general');
  }, [maxUsagePercentage, remainDays, settingsNavigator]);
  const cancel = useCallback(() => {
    maxUsagePercentage.reset();
    remainDays.reset();

    settingsNavigator('general');
  }, [maxUsagePercentage, remainDays, settingsNavigator]);

  useEffect(() => {
    fetchSizeInfoAsync();
  }, [fetchSizeInfoAsync]);

  return {
    currentUsageSize,
    diskTotalSize,
    maxUsagePercentage,
    remainDays,
    save,
    cancel,
    fetchSizeInfoAsync,
  };
}
