import { useState, useEffect } from 'react';
import { useI18n } from '../../i18n';
import { getUsername } from '../../java-api/launch-window';
import { AccountMeterInfo, getNickname, getAccountInfoAsync } from '../../java-api/main-window';

const INITIAL_ACCOUNT_METER_INFO: AccountMeterInfo = {
  isPaidUser: false,
  isInTeam: false,
  accountExpireLeftTime: 0,
  usedStorageSize: 0,
  totalStorageSize: 0,

  freeDownRate: 0,
  freeUpRate: 0,
  usedDownRate: 0,
  usedUpRate: 0,
  rateResetLeftMills: 0,
};

const MILLISECONDS_CONSTS = {
  ONE_DAY: 24 * 60 * 60 * 1000,
  ONE_HOUR: 60 * 60 * 1000,
  TWENTY_YEAYS: 20 * 365 * 24 * 60 * 60 * 1000,
};

function useExpirationTime(milliseconds: number) {
  const { $t } = useI18n();
  if (milliseconds > MILLISECONDS_CONSTS.TWENTY_YEAYS) {
    return $t('accountInfo.expire.paidUser.value.never');
  }

  if (milliseconds <= 0) {
    return $t('accountInfo.expire.paidUser.value.alreadyExpired');
  }

  return new Date(Date.now() + milliseconds).toLocaleDateString();
}

function useTrafficResetTime(milliseconds: number) {
  const { $t } = useI18n();

  if (milliseconds > MILLISECONDS_CONSTS.ONE_DAY) {
    return $t(
      'accountInfo.expire.freeUser.value.resetAfterDays',
      Math.floor(milliseconds / MILLISECONDS_CONSTS.ONE_DAY)
    );
  }

  if (milliseconds > MILLISECONDS_CONSTS.ONE_HOUR) {
    return $t(
      'accountInfo.expire.freeUser.value.resetAfterHours',
      Math.floor(milliseconds / MILLISECONDS_CONSTS.ONE_HOUR)
    );
  }

  return $t('accountInfo.expire.freeUser.value.resetWithin1Hour');
}

export default function useAccountInfoViewModel(selected: boolean) {
  const [username, setUsername] = useState('');
  const [nickname, setNickname] = useState('');
  const [
    {
      isPaidUser,
      isInTeam,

      usedStorageSize,
      totalStorageSize,
      accountExpireLeftTime,

      usedUpRate,
      freeUpRate,
      usedDownRate,
      freeDownRate,
      rateResetLeftMills,
    },
    setMeterInfo,
  ] = useState<AccountMeterInfo>(INITIAL_ACCOUNT_METER_INFO);
  const accountExpireLeftTimeText = useExpirationTime(accountExpireLeftTime);
  const trafficResetLeftTimeText = useTrafficResetTime(rateResetLeftMills);

  useEffect(() => {
    const fetchMeterInfo = async () => {
      setUsername(getUsername());
      setNickname(getNickname());
      setMeterInfo(await getAccountInfoAsync());
    };

    if (selected) {
      fetchMeterInfo();
    }
  }, [selected]);

  return {
    username,
    nickname,
    isPaidUser,
    isInTeam,
    usedStorageSize,
    totalStorageSize,
    usedUpRate,
    totalUpRate: freeUpRate + usedUpRate,
    usedDownRate,
    totalDownRate: freeDownRate + usedDownRate,
    expirationTimeText: isPaidUser ? accountExpireLeftTimeText : trafficResetLeftTimeText,
  };
}
