import React, {
  ReactNode,
  ReactNodeArray,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import classNames from 'classnames';
import { Size, useResizeObserver, useLazyRef } from '../../hooks';

import hamburgIcon from '../../assets/main-window/sidebar_hamburg.svg';
import { Switch } from './switch';
import { uuidv4 } from '../../helpers';
import { useNavigator } from './switch-host';
import { Tooltip } from '../tooltip';
import { useI18n } from '../../i18n';

type SidebarDisplayMode = 'inline' | 'overlay';

export type TabItem<TKey> = {
  key: TKey;
  header?: (key: TKey, selected: boolean, expanded: boolean) => ReactNode;
  content: (selected: boolean) => ReactNode;
};

interface HamburgTabControlProps<Tkey extends string> {
  itemsSource: TabItem<Tkey>[];
  selectedKey: Tkey;
  criticalSize: number;
  collapsedSidebarSize: number | string;
  expandedSidebarSize: number | string;
  footerBar?: ReactNode;
  sidebarAddon?: ReactNodeArray;
}

export function HamburgTabControl<TKey extends string>({
  itemsSource,
  selectedKey,
  criticalSize,
  collapsedSidebarSize,
  expandedSidebarSize,
  footerBar,
  sidebarAddon,
}: HamburgTabControlProps<TKey>) {
  const swicthName = useLazyRef<string>(() => `hamburg-tab-control-${uuidv4()}`);
  const wrapperRef = useRef<HTMLDivElement>(null);
  const [displayMode, setDisplayMode] = useState<SidebarDisplayMode>('overlay');
  const [isExpanded, setIsExpanded] = useState(false);
  const [, setSelectedKey] = useState(selectedKey);
  const { $t } = useI18n();
  const updateSidebarStatus = useCallback(
    (size: Size) => {
      const isBigView = size.width > criticalSize;
      const newDisplayMode: SidebarDisplayMode = isBigView ? 'inline' : 'overlay';

      if (newDisplayMode !== displayMode) {
        setDisplayMode(newDisplayMode);
        if (isBigView) {
          if (!isExpanded) {
            setIsExpanded(true);
          }
        } else {
          if (isExpanded) {
            setIsExpanded(false);
          }
        }
      }
    },
    [criticalSize, displayMode, isExpanded]
  );
  useResizeObserver(wrapperRef, updateSidebarStatus);

  const headers = useMemo(
    () =>
      itemsSource
        .filter((item) => !!item.header)
        .map((item) => item.header!(item.key, selectedKey === item.key, isExpanded)),
    [isExpanded, itemsSource, selectedKey]
  );

  const navigator = useNavigator(swicthName);
  useEffect(() => {
    setSelectedKey((prevValue) => {
      if (prevValue !== selectedKey) {
        // Notify the current view will be unloaded.
        navigator(prevValue, { selected: false });
      }

      navigator(selectedKey, { selected: true });

      return selectedKey;
    });
  }, [navigator, selectedKey]);

  const viewCases = useMemo(() => {
    return itemsSource.map((item) => {
      return {
        label: item.key,
        renderer: ({ selected }: { selected: boolean }) => item.content(selected),
      };
    });
  }, [itemsSource]);

  const wrapperClass = classNames('hamburg-tab-control-wrapper', displayMode, {
    expanded: isExpanded,
  });

  return (
    <div ref={wrapperRef} className={wrapperClass}>
      <div className="hamburg-view layer-0">
        <div
          className="hamburg-sidebar"
          style={{
            width:
              isExpanded && displayMode === 'inline' ? expandedSidebarSize : collapsedSidebarSize,
          }}
        />
        <div className="hamburg-content">
          <Switch name={swicthName} cases={viewCases} />
          {footerBar ? <div className="hamburg-footer-bar">{footerBar}</div> : null}
        </div>
      </div>
      <div className="hamburg-view layer-1">
        <div
          className="hamburg-sidebar"
          style={{ width: isExpanded ? expandedSidebarSize : collapsedSidebarSize }}
        >
          <Tooltip title={$t('humburg.button.tooltip')} placement='right'>
            <button className="hamburg-button" onClick={() => setIsExpanded(!isExpanded)}>
              <img src={hamburgIcon} alt="hamburg" width="20px" height="20px" />
            </button>
          </Tooltip>
          <div className="hamburg-tab-headers">{headers}</div>
          <div className="hamburg-sidebar-addon">{sidebarAddon}</div>
        </div>
        <div className="hamburg-mask" onClick={() => setIsExpanded(false)} />
      </div>
    </div>
  );
}
