import React from 'react';
import { SearchBox } from '../../components/input';
import { getFileIcon, getFileType } from '../../helpers';
import cancalIcon from '../../assets/main-window/bookmarks_cancel.svg';
import { VirtualizingListBox } from '../../components/items-control';
import { BookmarkItem, deleteBookmarkItemAsync } from '../../java-api/main-window';
import { openFileWithApp } from '../../java-api/common-api';
import refreshIcon from '../../assets/main-window/refresh.svg';
import HighlightSpan from '../../components/highlight-span';
import useBookmarksViewModel from '../../models/main-window/bookmarks-view-model';
import { useI18n } from '../../i18n';
import EmptyView from './empty-view';
import { Tooltip } from '../../components/tooltip';

type BookmarksViewProps = {
  selected: boolean;
};

type BookmarkListBoxItemProps = {
  item: BookmarkItem;
  queriesText: string;
  deleteSelf: () => void;
};

function BookmarkListBoxItem({ item, queriesText, deleteSelf }: BookmarkListBoxItemProps) {
  const { $t } = useI18n();
  const fileType = item.isDir ? 'folder' : getFileType(item.name);
  return (
    <div className="bookmark-list-box-item">
      <img className="bookmark-file-icon" src={getFileIcon(fileType)} alt={fileType} />
      <HighlightSpan
        className="text-bookmark-file-name text ellipsis link"
        sourceText={item.name}
        queriesText={queriesText}
        onClick={() => openFileWithApp(item.fullPath)}
      />
      <Tooltip title={$t('bookmarksView.button.remove.tooltip')}>
        <button className="bookmark-button" onClick={deleteSelf}>
          <img src={cancalIcon} alt="close" />
        </button>
      </Tooltip>
    </div>
  );
}

export default function BookmarksView({ selected }: BookmarksViewProps) {
  const { $t } = useI18n();
  const {
    searchBoxRef,
    isSearchMode,
    bookmarkItems,
    setIsSearchMode,
    filterBookmarkItemsThrottle,
    fetchBookmarkItems,
  } = useBookmarksViewModel(selected);

  return (
    <div className="bookmarks-view-wrapper">
      <div className="search-box-region">
        <SearchBox
          ref={searchBoxRef}
          placeholder={$t('bookmarks.search.placeholder')}
          isEditing={isSearchMode}
          setIsEditing={setIsSearchMode}
          onChange={(e) => filterBookmarkItemsThrottle(e.target.value)}
          addon={
            <Tooltip title={$t('bookmarksView.button.refresh.tooltip')}>
              <button className="search-addon-button" onClick={() => fetchBookmarkItems()}>
                <img src={refreshIcon} alt="refresh-icon" />
              </button>
            </Tooltip>
          }
        />
      </div>
      <div className="main-content-region">
        {bookmarkItems && bookmarkItems.length ? (
          <VirtualizingListBox
            sizeProvider={{ itemSize: 52, itemCount: bookmarkItems.length }}
            renderItems={(startIndex, endIndex) =>
              bookmarkItems.slice(startIndex, endIndex).map((item) => (
                <BookmarkListBoxItem
                  item={item}
                  queriesText={searchBoxRef.current?.value ?? ''}
                  deleteSelf={async () => {
                    await deleteBookmarkItemAsync(item.fullPath);
                    fetchBookmarkItems();
                  }}
                />
              ))
            }
          />
        ) : (
          <EmptyView
            title={$t('bookmarks.search.emptyView.title')}
            subtitle={$t('bookmarks.search.emptyView.description')}
          />
        )}
      </div>
    </div>
  );
}
