import React from 'react';
import { getDisplayStringBytes, getFileIcon, getFileType } from '../../helpers';
import { useI18n } from '../../i18n';
import { openPath } from '../../java-api/common-api';
import { SyncStatusPopupData } from '../../models/main-window/sync-state-bar-model';

type SyncStatePopupProps = {
  data: SyncStatusPopupData;
};

type DisplayFileItemProps = {
  fileName: string;
  bottomText: string;
  rightTopText?: string;
  rightBottomText?: string;
  openFile: () => void;
};

function TriangleIcon() {
  return (
    <svg
      className="sync-state-popup-triangle"
      x="0px"
      y="0px"
      viewBox="0 0 18 8"
      width="18px"
      height="8px"
    >
      <g>
        <g>
          <rect
            x="2"
            width="14"
            height="1"
            style={{ fillRule: 'evenodd', clipRule: 'evenodd', fill: 'var(--background-color)' }}
          />
          <path
            className="st1"
            style={{
              fillRule: 'evenodd',
              clipRule: 'evenodd',
              fill: 'var(--background-color)',
              stroke: 'var(--border-color)',
            }}
            d="M0,0.5h2.5l5,5.8c0.7,0.8,2,0.9,2.8,0.2c0.1-0.1,0.1-0.1,0.2-0.2l5-5.8l0,0H18"
          />
        </g>
      </g>
    </svg>
  );
}

function DisplayFileItem({
  fileName,
  bottomText,
  rightTopText,
  rightBottomText,
  openFile,
}: DisplayFileItemProps) {
  return (
    <div className="display-file-item" onClick={openFile}>
      <img className="file-icon" src={getFileIcon(getFileType(fileName))} alt="file icon" />
      <span
        className="text subtitle ellipsis link file-name"
        style={{ gridColumn: rightTopText ? undefined : '3 / 6' }}
      >
        {fileName}
      </span>
      {rightTopText ? (
        <span className="text tip-secondary ellipsis right-top">{rightTopText}</span>
      ) : null}
      <span
        className="text tip-secondary ellipsis bottom"
        style={{ gridColumn: rightBottomText ? undefined : '3 / 6' }}
      >
        {bottomText}
      </span>
      {rightBottomText ? (
        <span className="text tip-secondary ellipsis right-bottom">{rightBottomText}</span>
      ) : null}
    </div>
  );
}

function usePopupContent(data: SyncStatusPopupData) {
  const { $t } = useI18n();
  switch (data.type) {
    case 'locked-files':
      return data.files.map((item) => (
        <DisplayFileItem
          fileName={item.fileName}
          bottomText={item.lockerName}
          rightBottomText={item.lockedTime}
          openFile={() => openPath(item.fullPath)}
        />
      ));
    case 'paused':
      return (
        <DisplayFileItem
          fileName={data.fileName}
          bottomText={$t('syncStatus.popup.sizeFormat', getDisplayStringBytes(data.totalSize))}
          rightTopText={$t('syncStatus.popup.paused')}
          openFile={() => openPath(data.filePath)}
        />
      );
    case 'analyzing':
      return (
        <DisplayFileItem
          fileName={$t('syncStatus.popup.analyingFileNameFormat', data.fileName)}
          bottomText={$t('syncStatus.popup.sizeFormat', getDisplayStringBytes(data.totalSize))}
          openFile={() => openPath(data.filePath)}
        />
      );
    case 'transferring':
      return (
        <DisplayFileItem
          fileName={data.fileName}
          rightTopText={`${getDisplayStringBytes(data.instantaneousSpeed)}/s`}
          bottomText={$t('syncStatus.popup.sizeFormat', getDisplayStringBytes(data.totalSize))}
          rightBottomText={$t('syncStatus.popup.remainingTimeFormat', data.remainingTime)}
          openFile={() => openPath(data.filePath)}
        />
      );
  }
}

export default function SyncStatePopup({ data }: SyncStatePopupProps) {
  const content = usePopupContent(data);

  return (
    <div className="sync-state-popup-wrapper" onWheel={(e) => e.stopPropagation()}>
      <div className="sync-state-popup-content">{content}</div>
      <TriangleIcon />
    </div>
  );
}
