import React from 'react';
import { Button } from '../../components/button';
import { getDisplayStringBytes } from '../../helpers';
import { useI18n } from '../../i18n';
import { openPricingLink, openTrafficPolicyLink, switchAccount } from '../../java-api/main-window';
import useAccountInfoViewModel from '../../models/main-window/account-info-view-model';
import { SettingsFooterButton, SettingsViewTemplate } from './settings-view-template';

type UsageData = {
  used: number;
  total: number;
};

function PropertyItem({ propertyName, value }: { propertyName: string; value: string }) {
  return (
    <div className="property-item-wrapper">
      <span className="text property-key">{propertyName}</span>
      <span className="text property-value">{value}</span>
    </div>
  );
}

function FreeAccountUsageRegion({
  upUsage,
  downUsage,
}: {
  upUsage: UsageData;
  downUsage: UsageData;
}) {
  const { $t } = useI18n();
  return (
    <div className="account-usage-region">
      <span className="text property-key">{$t('accountInfo.usage')}</span>
      <span className="text property-value">
        {$t(
          'accountInfo.uploadTraffic',
          `${getDisplayStringBytes(upUsage.used)} / ${getDisplayStringBytes(upUsage.total)}`
        )}
      </span>
      <progress className="progress" max={upUsage.total} value={upUsage.used} />
      <span className="text property-value">
        {$t(
          'accountInfo.downloadTraffic',
          `${getDisplayStringBytes(downUsage.used)} / ${getDisplayStringBytes(downUsage.total)}`
        )}
      </span>
      <progress className="progress" max={downUsage.total} value={downUsage.used} />
    </div>
  );
}

function PaidAccountUsageRegion({ usage }: { usage: UsageData }) {
  const { $t } = useI18n();
  return (
    <div className="account-usage-region">
      <span className="text property-key">{$t('accountInfo.usage')}</span>
      <span className="text property-value">{`${getDisplayStringBytes(
        usage.used
      )} / ${getDisplayStringBytes(usage.total)}`}</span>
      <progress className="progress" max={usage.total} value={usage.used} />
    </div>
  );
}

type AccountInfoViewProps = {
  selected: boolean;
};

export default function AccountInfoView({ selected }: AccountInfoViewProps) {
  const { $t } = useI18n();
  const {
    username,
    nickname,
    isPaidUser,
    isInTeam,
    usedStorageSize,
    totalStorageSize,
    usedUpRate,
    totalUpRate,
    usedDownRate,
    totalDownRate,
    expirationTimeText,
  } = useAccountInfoViewModel(selected);

  return (
    <SettingsViewTemplate
      header={$t('accountInfo.title')}
      content={
        <div className="account-info-content">
          <PropertyItem propertyName={$t('accountInfo.account')} value={username} />
          <PropertyItem propertyName={$t('accountInfo.nickname')} value={nickname} />
          <PropertyItem propertyName={$t('accountInfo.expire')} value={expirationTimeText} />
          {isPaidUser ? (
            <PaidAccountUsageRegion usage={{ used: usedStorageSize, total: totalStorageSize }} />
          ) : (
            <FreeAccountUsageRegion
              upUsage={{ used: usedUpRate, total: totalUpRate }}
              downUsage={{ used: usedDownRate, total: totalDownRate }}
            />
          )}
          <div className="account-links">
            {isPaidUser ? (
              isInTeam ? null : (
                <Button buttonType="link" onClick={() => openPricingLink()}>
                  {$t('accountInfo.renew')}
                </Button>
              )
            ) : (
              <>
                <Button buttonType="link" onClick={() => openTrafficPolicyLink()}>
                  {$t('accountInfo.viewTrafficRules')}
                </Button>
                <Button buttonType="link" onClick={() => openPricingLink()}>
                  {$t('accountInfo.accountUpgrade')}
                </Button>
              </>
            )}
          </div>
        </div>
      }
      footer={
        <div className="settings-view footer-account-info">
          <div className="split-line"></div>
          <SettingsFooterButton
            text={$t('accountInfo.switchAccount')}
            onClick={async () => await switchAccount()}
          />
        </div>
      }
    />
  );
}
