import produce from 'immer';
import { useState, useEffect, useCallback } from 'react';
import { removeAll } from '../../helpers/list-helpers';
import {
  SearchResult,
  getSearchResultsAsync,
  setSearchHistory,
  getSearchHistory,
} from '../../java-api/main-window';

export default function useSyncFolderSearchResultViewModel(
  searchText?: string,
  showDetails?: boolean
) {
  const [searchHistoryList, setSearchHistoryList] = useState<string[]>([]);
  const [searchResultItems, setSearchResultItems] = useState<SearchResult[]>([]);
  useEffect(() => setSearchHistoryList(getSearchHistory()), []);
  useEffect(() => setSearchHistory(searchHistoryList), [searchHistoryList]);
  useEffect(() => {
    const searchString = searchText?.trim();
    if (!searchString) return;

    if (showDetails) {
      setSearchHistoryList((oldValue) =>
        produce(oldValue, (draft) => {
          removeAll(draft, searchString);
          draft.unshift(searchString);
        })
      );
    }
    const fetch = async () => {
      const results = await getSearchResultsAsync(searchString, !!showDetails);
      setSearchResultItems(results);
    };

    fetch();
  }, [searchText, showDetails]);

  const removeHistory = useCallback(
    (index) =>
      setSearchHistoryList((oldValue) =>
        produce(oldValue, (draft) => {
          draft.splice(index, 1);
        })
      ),
    []
  );
  const clearHistory = useCallback(() => setSearchHistoryList([]), []);

  return {
    searchHistoryList,
    searchResultItems,
    removeHistory,
    clearHistory,
  };
}
