import React from 'react';
import emptyIcon from '../../assets/main-window/search_empty.svg';

type EmptyViewProps = {
  title: string;
  subtitle?: string;
};

export default function EmptyView({ title, subtitle }: EmptyViewProps) {
  return (
    <div className="empty-view-wrapper">
      <img className="empty-icon" src={emptyIcon} alt="empty" />
      <span className="text subtitle label-empty-title">{title}</span>
      {subtitle ? (
        <span className="text tip-secondary label-empty-description">{subtitle}</span>
      ) : null}
    </div>
  );
}
