import { useEffect, useMemo } from 'react';
import { useAsyncConfigureValue, useConfigureValue } from '../../hooks/use-configure-value';
import { LanguageCode, useI18n } from '../../i18n';
import { isChineseLocale } from '../../java-api';
import {
  getAutoStartAsync,
  setAutoStartAsync,
  getNotifyOnFilesUpdatedAsync,
  setNotifyOnFilesUpdatedAsync,
  getSafeGuardChangeAsync,
  setSafeGuardChangeAsync,
  getFileProtectionLockAsync,
  setFileProtectionLockAsync,
  getShowDockIconChangeAsync,
  setShowDockIconChangeAsync,
  getExperimentalUserChangeAsync,
  setExperimentalUserChangeAsync,
  setCultureAsync,
  BadgePreference,
  getBadgePreference,
  setBadgePreference,
} from '../../java-api/main-window';

export default function useSettingsGeneralViewModel() {
  const autoStart = useAsyncConfigureValue(getAutoStartAsync, setAutoStartAsync, false);
  const notifyOnFilesUpdated = useAsyncConfigureValue(
    getNotifyOnFilesUpdatedAsync,
    setNotifyOnFilesUpdatedAsync,
    false
  );
  const safeGuard = useAsyncConfigureValue(getSafeGuardChangeAsync, setSafeGuardChangeAsync, false);
  const fileProtectionLock = useAsyncConfigureValue(
    getFileProtectionLockAsync,
    setFileProtectionLockAsync,
    false
  );
  const showInDocker = useAsyncConfigureValue(
    getShowDockIconChangeAsync,
    setShowDockIconChangeAsync,
    false
  );
  const experimental = useAsyncConfigureValue(
    getExperimentalUserChangeAsync,
    setExperimentalUserChangeAsync,
    false
  );
  const { $t } = useI18n();
  const languageItems = useMemo(
    () => [
      { value: 'zh-CN', label: $t('settings.general.language.comboItems.chinese') },
      { value: 'en-US', label: $t('settings.general.language.comboItems.english') },
    ],
    [$t]
  );
  const language = useAsyncConfigureValue<LanguageCode>(
    () => Promise.resolve(isChineseLocale() ? 'zh-CN' : 'en-US'),
    setCultureAsync,
    'zh-CN'
  );

  const badgePreferenceItems = useMemo(
    () => [
      {value: BadgePreference.alwaysShow, label: $t('settings.general.badgePreference.alwaysShow')},
      {value: BadgePreference.presentIfNecessary, label: $t('settings.general.badgePreference.presentIfNecessary')},
      {value: BadgePreference.neverShow, label: $t('settings.general.badgePreference.neverShow')},      
    ],
    [$t]
  );

  const badgePreference = useConfigureValue<number>(
    getBadgePreference,
    setBadgePreference
  );

  useEffect(() => {
    autoStart.reset(true);
    notifyOnFilesUpdated.reset(true);
    safeGuard.reset(true);
    fileProtectionLock.reset(true);
    showInDocker.reset(true);
    experimental.reset(true);
    language.reset(true);
    badgePreference.reset(true);
  }, [
    autoStart,
    experimental,
    fileProtectionLock,
    language,
    notifyOnFilesUpdated,
    safeGuard,
    showInDocker,
    badgePreference,
  ]);

  return {
    autoStart,
    notifyOnFilesUpdated,
    safeGuard,
    fileProtectionLock,
    showInDocker,
    experimental,
    languageItems,
    language,
    badgePreferenceItems,
    badgePreference
  };
}
