import { useState, useRef, useEffect, useCallback } from 'react';
import { useNavigator } from '../../components/items-control';
import { useThrottle } from '../../hooks/use-throttle';
import {
  SyncFolderItem,
  getSyncFolderItemsAsync,
  subscribeMainWindowEvent,
  getPinnedSandboxIdListAsync,
  setIsPinnedAsync,
} from '../../java-api/main-window';

export const SWITCH_NAME_SYNC_FOLDER_CONTENT = 'sync-folder-content';

export type MainContentViewName = 'normal' | 'search-result';

export function useSyncFolderMainContentModel() {
  const [syncFolderItems, setSyncFolderItems] = useState<SyncFolderItem[]>([]);
  const [pinnedSandboxIdList, setPinnedSandboxIdList] = useState<ReadonlyArray<number>>([]);

  useEffect(() => {
    const fetch = async () => setSyncFolderItems(await getSyncFolderItemsAsync());
    const updateSandboxEventToken = subscribeMainWindowEvent('update-sandbox', () => fetch());

    fetch();
    return () => updateSandboxEventToken();
  }, []);

  useEffect(() => {
    const fetch = async () => setPinnedSandboxIdList(await getPinnedSandboxIdListAsync());

    fetch();
  }, []);

  const setIsPinned = useCallback(async (id: number, value: boolean) => {
    await setIsPinnedAsync(id, value);
    setPinnedSandboxIdList(await getPinnedSandboxIdListAsync());
  }, []);

  return {
    syncFolderItems,
    pinnedSandboxIdList,
    setIsPinned,
  };
}

export default function useSyncFolderViewModel() {
  const contentNavigator = useNavigator<MainContentViewName>(SWITCH_NAME_SYNC_FOLDER_CONTENT);

  const [isSearchMode, setIsSearchMode] = useState(false);

  const searchTextRef = useRef('');
  const searchBoxRef = useRef<HTMLInputElement>(null);

  const setSearchText = useCallback(
    (value: string) => {
      if (searchBoxRef.current) {
        searchBoxRef.current.value = value;
        searchBoxRef.current.focus();
        searchTextRef.current = value;
        contentNavigator('search-result', {
          searchText: value,
          setSearchText,
        });
      }
    },
    [contentNavigator]
  );

  useEffect(() => {
    const searchBoxElemenet = searchBoxRef.current;
    if (searchBoxElemenet) {
      const handler = (e: KeyboardEvent) => {
        if (e.key === 'Enter') {
          contentNavigator('search-result', {
            searchText: searchTextRef.current,
            showDetails: true,
            setSearchText,
          });
        }
      };
      searchBoxElemenet.addEventListener('keypress', handler);
      return () => searchBoxElemenet.removeEventListener('keypress', handler);
    }
  }, [contentNavigator, setSearchText]);
  useEffect(
    () =>
      contentNavigator(
        isSearchMode ? 'search-result' : 'normal',
        isSearchMode ? { setSearchText } : {}
      ),
    [contentNavigator, isSearchMode, setSearchText]
  );
  useEffect(() => {
    if (!isSearchMode && searchBoxRef.current) {
      searchBoxRef.current.value = '';
    }
  }, [isSearchMode]);

  const onSearchTextChanged = useCallback(
    (searchText: string) => {
      searchTextRef.current = searchText;
      contentNavigator('search-result', {
        searchText: searchTextRef.current,
        setSearchText,
      });
    },
    [contentNavigator, setSearchText]
  );
  const onSearchTextChangedThrottle = useThrottle(onSearchTextChanged, 300);

  return {
    searchBoxRef,
    isSearchMode,
    setIsSearchMode,
    onSearchTextChangedThrottle,
  };
}
