import { useState, useRef, useEffect, useMemo } from 'react';
import { useI18n } from '../../i18n';
import {
  SyncStatusMessage,
  LockedFileItem,
  subscribeMainWindowEvent,
  getLockedFileItemsAsync,
  getSyncingInfoAsync,
  getSyncErrorCount,
  isSandboxOwnerExpired,
} from '../../java-api/main-window';

type LockedFilesData = {
  type: 'locked-files';
  files: ReadonlyArray<LockedFileItem>;
};

type TransferringData = {
  type: 'transferring';
  fileName: string;
  filePath: string;
  instantaneousSpeed: number;
  totalSize: number;
  remainingTime: string;
};

type PausedData = {
  type: 'paused';
  fileName: string;
  filePath: string;
  totalSize: number;
};

type AnalyzingData = {
  type: 'analyzing';
  fileName: string;
  filePath: string;
  totalSize: number;
};

export type SyncStatusPopupData = LockedFilesData | TransferringData | PausedData | AnalyzingData;

export default function useSyncStateBarModel() {
  const { $t } = useI18n();
  const defaultStatus = useMemo<SyncStatusMessage>(
    () => ({
      isPaused: false,
      type: 'syncing',
      details: $t('syncStatus.message.initializing'),
    }),
    [$t]
  );
  const [syncStatusMessage, setSyncStatusMessage] = useState<SyncStatusMessage>();
  const [popupData, setPopupData] = useState<SyncStatusPopupData>();
  const isSyncedRef = useRef(false);
  const isPausedRef = useRef(false);

  const { isPaused, type, details } = syncStatusMessage ?? defaultStatus;
  isSyncedRef.current = type === 'synced';
  isPausedRef.current = isPaused;

  useEffect(() => {
    const dispose = subscribeMainWindowEvent('update-sync-status', async (message) => {
      setSyncStatusMessage(message);

      setPopupData(await getPopupData(isSyncedRef.current, isPausedRef.current));
    });

    return () => dispose();
  }, []);

  const syncErrorCount = type === 'synced' ? getSyncErrorCount() : 0;
  const sandboxOwnerExpired = isSandboxOwnerExpired();
  return { isPaused, type, details, popupData, syncErrorCount, sandboxOwnerExpired };
}

async function getPopupData(isSynced: boolean, isPaused: boolean) {
  if (isSynced) {
    // Synced (completed): display locked files if exists.
    const lockedFiles = await getLockedFileItemsAsync();
    if (!lockedFiles.length) return undefined;

    const lockedFilesData: LockedFilesData = {
      type: 'locked-files',
      files: lockedFiles,
    };
    return lockedFilesData;
  } else {
    // Sync uncompleted: paused, transferring, analyzing
    const info = await getSyncingInfoAsync();
    if (!info.fileName || !info.filePath) return undefined;

    if (isPaused) {
      // paused
      const pausedData: PausedData = {
        type: 'paused',
        fileName: info.fileName,
        filePath: info.filePath,
        totalSize: info.totalSize,
      };
      return pausedData;
    } else {
      if (info.instantaneousSpeed < 1e-6) {
        // Analyzing
        const analyzingData: AnalyzingData = {
          type: 'analyzing',
          fileName: info.fileName,
          filePath: info.filePath,
          totalSize: info.totalSize,
        };
        return analyzingData;
      } else {
        // Transferring
        const transferringData: TransferringData = {
          type: 'transferring',
          fileName: info.fileName,
          filePath: info.filePath,
          totalSize: info.totalSize,
          instantaneousSpeed: info.instantaneousSpeed,
          remainingTime: info.remainingTime,
        };
        return transferringData;
      }
    }
  }
}
