import React from 'react';
import { Button } from '../../components/button';
import { useBannerImages, useI18n } from '../../i18n';
import { AppInfo, appInfoList } from '../../models/main-window/app-info';

import { ScrollViewer } from '../../components/layout-container';
import { Carousel } from '../../components/items-control';
import { openUrl } from '../../java-api';
import { openTemplateInLightApp } from '../../java-api/main-window';

type BannerImageProps = {
  image: string;
  onClick: () => void;
};

function AppCard({ i18nKey, icon, platforms, onClick }: AppInfo) {
  const { $t } = useI18n();
  return (
    <li className="app-recommend-item">
      <img className="app-card-icon" src={icon} alt="app-icon" />
      <span className="text-app-card-name">{$t(`appRecommend.name.${i18nKey}` as any)}</span>
      <div className="app-card-platforms">
        {platforms.map((platform) => (
          <span key={platform} className="text-app-card-platform">
            {$t(`appRecommend.platform.${platform}` as any)}
          </span>
        ))}
      </div>
      <Button
        className="app-card-button"
        buttonType="primary"
        style={{ width: '80px', height: '32px' }}
        onClick={onClick}
      >
        {$t('button.open')}
      </Button>
      <span className="text-app-card-description">
        {$t(`appRecommend.description.${i18nKey}` as any)}
      </span>
    </li>
  );
}

function BannerImage({ image, onClick }: BannerImageProps) {
  return (
    <img
      className="banner-image"
      key={image}
      src={image}
      alt={image}
      onClick={onClick}
      style={{ cursor: 'pointer' }}
    />
  );
}

export default function AppRecommendView() {
  const { banner1, banner2, banner3, banner4 } = useBannerImages();

  return (
    <div className="app-recommend-view-wrapper">
      <ScrollViewer>
        <div className="app-recommend-content">
          <div className="app-recommend-banner">
            <Carousel autoplay slideWidth="100%" slideHeight="96px">
              <BannerImage
                image={banner1}
                onClick={() =>
                  openUrl('https://www.jianguoyun.com/static/html/joint_product/index.html')
                }
              />
              <BannerImage
                image={banner2}
                onClick={() => openUrl('https://assignmentassist.jianguoyun.com/')}
              />
              <BannerImage image={banner3} onClick={() => openTemplateInLightApp('nbmx')} />
              <BannerImage image={banner4} onClick={() => openTemplateInLightApp('nol')} />
            </Carousel>
          </div>
          <ul className="app-recommend-list">
            {appInfoList.map((info) => (
              <AppCard key={info.i18nKey} {...info} />
            ))}
          </ul>
        </div>
      </ScrollViewer>
    </div>
  );
}
