import React, { useEffect, useMemo, useState } from 'react';
import { HamburgTabControl, TabItem } from '../../components/items-control';

import SyncFolderView from './sync-folder-view';
import RecentView from './recent-view';
import BookmarksView from './bookmarks-view';
import AppRecommendView from './app-recommend-view';
import SyncStateBar from './sync-state-bar';
import { useI18n } from '../../i18n';
import AccountInfoView from './account-info-view';
import SettingsView from './settings-view';
import {
  clearRecentEventUnreadCount,
  executeCallbackEventsAsync,
  getRecentEventUnreadCount,
  isLightAppReady,
  openHomePage,
  subscribeMainWindowEvent,
} from '../../java-api/main-window';
import { useShouldShowUpgradeToFileProviderEntry} from '../../java-api';
import { HamburgTabHeaderItem } from '../../components/items-control/hamburg-tab-header-item';
import classNames from 'classnames';

import recommendIcon from '../../assets/main-window/sidebar_recommend.svg';
import webIcon from '../../assets/main-window/sidebar_web.svg';
import { ReactComponent as FolderIcon } from '../../assets/main-window/sidebar_folder.svg';
import { ReactComponent as RecentIcon } from '../../assets/main-window/sidebar_recent.svg';
import { ReactComponent as BookmarksIcon } from '../../assets/main-window/sidebar_bookmarks.svg';
import { ReactComponent as AccountIcon } from '../../assets/main-window/sidebar_account.svg';
import { ReactComponent as SettingsIcon } from '../../assets/main-window/sidebar_settings.svg';
import { Tooltip } from '../../components/tooltip';

type TabItemKey =
  | 'sync-folder'
  | 'recent'
  | 'bookmarks'
  | 'app-recommend'
  | 'account-info'
  | 'settings';

type RecentEventHeaderItemProps = {
  itemkey: TabItemKey;
  selected: boolean;
  expanded: boolean;
  setSelectedKey: (value: TabItemKey) => void;
};

function getIconColor(selected: boolean) {
  return selected ? 'var(--accent-color)' : 'var(--text-secondary-color)';
}

function RecentEventHeaderItem({
  itemkey,
  selected,
  expanded,
  setSelectedKey,
}: RecentEventHeaderItemProps) {
  const { $t } = useI18n();
  const [unreadedCount, setUnreadedCount] = useState(0);
  useEffect(() => {
    setUnreadedCount(getRecentEventUnreadCount());
    const dispose = subscribeMainWindowEvent('update-recent-event', ({ unreadCount }) =>
      setUnreadedCount(unreadCount)
    );

    return () => dispose();
  }, []);

  return (
    <HamburgTabHeaderItem<TabItemKey>
      itemKey={itemkey}
      selected={selected}
      isExpanded={expanded}
      header={{
        icon: <RecentIcon style={{ fill: getIconColor(selected) }} />,
        text: $t('recent.title'),
        changeCount: unreadedCount,
      }}
      setSelectedKey={setSelectedKey}
      onClick={() => {
        clearRecentEventUnreadCount();
        setUnreadedCount(0);
      }}
    />
  );
}

export default function MainWindow() {
  const { $t } = useI18n();
  const [selectedKey, setSelectedKey] = useState<TabItemKey>('sync-folder');
  useEffect(() => {
    const dispose = subscribeMainWindowEvent('goto-settings-tab', () => {
      setSelectedKey('settings');
    });
    executeCallbackEventsAsync();

    return () => dispose();
  }, []);
  const tabItems = useMemo<TabItem<TabItemKey>[]>(() => {
    const views: TabItem<TabItemKey>[] = [
      {
        key: 'sync-folder',
        header: (key, selected, expanded) => (
          <HamburgTabHeaderItem<TabItemKey>
            key={key}
            itemKey={key}
            selected={selected}
            isExpanded={expanded}
            header={{
              icon: <FolderIcon style={{ fill: getIconColor(selected) }} />,
              text: $t('syncFolder.title'),
            }}
            setSelectedKey={setSelectedKey}
          />
        ),
        content: () => <SyncFolderView />,
      },
      {
        key: 'recent',
        header: (key, selected, expanded) => (
          <RecentEventHeaderItem
            key={key}
            itemkey={key}
            selected={selected}
            expanded={expanded}
            setSelectedKey={setSelectedKey}
          />
        ),
        content: () => <RecentView />,
      },
      {
        key: 'bookmarks',
        header: (key, selected, expanded) => (
          <HamburgTabHeaderItem<TabItemKey>
            key={key}
            itemKey={key}
            selected={selected}
            isExpanded={expanded}
            header={{
              icon: <BookmarksIcon style={{ fill: getIconColor(selected) }} />,
              text: $t('bookmarks.title'),
            }}
            setSelectedKey={setSelectedKey}
          />
        ),
        content: (selected) => <BookmarksView selected={selected} />,
      },
      {
        key: 'account-info',
        content: (selected) => <AccountInfoView selected={selected} />,
      },
      {
        key: 'settings',
        content: () => <SettingsView />,
      },
    ];

    if (isLightAppReady()) {
      views.push({
        key: 'app-recommend',
        header: (key, selected, expanded) => (
          <HamburgTabHeaderItem<TabItemKey>
            key={key}
            itemKey={key}
            selected={selected}
            isExpanded={expanded}
            header={{ icon: recommendIcon, text: $t('appRecommend.title') }}
            setSelectedKey={setSelectedKey}
          />
        ),
        content: () => <AppRecommendView />,
      });
    }

    return views;
  }, [$t]);

  const selectedAccountInfo = selectedKey === 'account-info';
  const selectedSettings = selectedKey === 'settings';

  const accountButtonClass = classNames('sidebar-button', {
    selected: selectedAccountInfo,
  });
  const settingsButtonClass = classNames('sidebar-button', {
    selected: selectedSettings,
  });

  const shouldShowUpgradeToFileProviderEntry = useShouldShowUpgradeToFileProviderEntry();

  return (
    <HamburgTabControl
      criticalSize={600}
      collapsedSidebarSize="56px"
      expandedSidebarSize="174px"
      selectedKey={selectedKey}
      itemsSource={tabItems}
      sidebarAddon={[
        <Tooltip title={$t('mainWindow.web.tooltip')} placement='right'>
          <button key="web-button" className="sidebar-button" onClick={() => openHomePage()}>
            <img src={webIcon} alt="web" width="20px" height="20px" />
          </button>
        </Tooltip>,
        <Tooltip title={$t('mainWindow.account.tooltip')} placement='right'>
          <button
            key="account-button"
            className={accountButtonClass}
            onClick={() => setSelectedKey('account-info')}
          >
            <AccountIcon
              width="20px"
              height="20px"
              style={{ fill: getIconColor(selectedAccountInfo) }}
            />
          </button>
        </Tooltip>,
        <Tooltip title={$t('mainwindow.setting.tooltip')} placement='right'>
          <button
            key="settings-button"
            className={settingsButtonClass}
            onClick={() => setSelectedKey('settings')}
          >
            <div className='setting-icon-container'>
              <SettingsIcon
                width="20px"
                height="20px"
                style={{ fill: getIconColor(selectedSettings) }}
              />
              {shouldShowUpgradeToFileProviderEntry ? (
                <div className='setting-icon-red-dot' />
              ) : null}
            </div>
          </button>
        </Tooltip>,
      ]}
      footerBar={
        selectedKey === 'account-info' || selectedKey === 'settings' ? null : <SyncStateBar />
      }
    />
  );
}
