import React, { useEffect, useMemo } from 'react';
import { CaseItem, Switch, useNavigator } from '../../components/items-control';
import { useIsOSXPlatform } from '../../java-api';
import SettingsAboutView from './settings-about-view';
import SettingsGeneralView from './settings-general-view';
import SettingsNetworkView from './settings-network-view';
import SettingsStorageView from './settings-storage-view';

export const SWITCH_NAME_SETTINGS = 'switch-settings';
export type SettingsViewName = 'general' | 'network' | 'storage' | 'about';

export default function SettingsView() {
  const settingsNavigator = useNavigator<SettingsViewName>(SWITCH_NAME_SETTINGS);
  useEffect(() => settingsNavigator('general'), [settingsNavigator]);
  const isOSXPlatform = useIsOSXPlatform();
  const settingCases = useMemo<CaseItem<SettingsViewName>[]>(() => {
    const settings: CaseItem<SettingsViewName>[] = [
      { label: 'general', renderer: () => <SettingsGeneralView /> },
      { label: 'network', renderer: () => <SettingsNetworkView /> },
      { label: 'about', renderer: () => <SettingsAboutView /> },
    ];

    // Linux platform don't support cloud bridge
    if (isOSXPlatform) {
      settings.push({ label: 'storage', renderer: () => <SettingsStorageView /> });
    }

    return settings;
  }, [isOSXPlatform]);
  return <Switch<SettingsViewName> name={SWITCH_NAME_SETTINGS} cases={settingCases} />;
}
