import React from 'react';
import SliderBar from '../../components/input/slider-bar';
import { useI18n } from '../../i18n';
import { SettingsViewTemplate } from './settings-view-template';
import NumericInput from '../../components/input/numeric-input';
import { Button } from '../../components/button';
import { getDisplayStringBytes } from '../../helpers';
import useSettingsStorageViewModel from '../../models/main-window/settings-storage-view-model';

import refreshIcon from '../../assets/main-window/refresh.svg';

export default function SettingsStorageView() {
  const { $t } = useI18n();
  const {
    currentUsageSize,
    diskTotalSize,
    maxUsagePercentage,
    remainDays,
    save,
    cancel,
    fetchSizeInfoAsync,
  } = useSettingsStorageViewModel();

  return (
    <SettingsViewTemplate
      header={$t('settings.storage.title')}
      onHeaderClicked={cancel}
      content={
        <div className="settings-view content-storage">
          <span className="text tip-secondary label-description">
            {$t('settings.storage.description')}
          </span>
          <span className="text subtitle label-current-usage">
            {$t('settings.storage.currentUsage')}
          </span>
          <div className="current-usage-value">
            <span className="text subtitle label-current-usage-value">
              {getDisplayStringBytes(currentUsageSize)}
            </span>
            <button className="button-current-usage-value" onClick={fetchSizeInfoAsync}>
              <img src={refreshIcon} alt="refresh" />
            </button>
          </div>
          <span className="text subtitle label-max-usage">{$t('settings.storage.maxUsage')}</span>
          <span className="text subtitle label-max-usage-value">{`${
            maxUsagePercentage.value
          }% (${getDisplayStringBytes((maxUsagePercentage.value * diskTotalSize) / 100)})`}</span>
          <SliderBar
            min={1}
            className="slider-bar-max-usage"
            value={maxUsagePercentage.value}
            setValue={(value) => (maxUsagePercentage.value = value)}
          />
          <span className="text subtitle label-cleaning-cycle">
            {$t('settings.storage.cleaningCycle')}
          </span>
          <NumericInput
            className="numeric-input-cleaning-cycle"
            min={1}
            max={90}
            value={remainDays.value}
            exitEdit={() =>
              (remainDays.value = Number.isNaN(remainDays.value) ? 1 : remainDays.value)
            }
            setValue={(set) => {
              if (typeof set === 'number') {
                remainDays.value = set;
              } else {
                remainDays.value = set(remainDays.value);
              }
            }}
          />
        </div>
      }
      footer={
        <div className="settings-view footer-storage">
          <Button onClick={cancel}>{$t('button.cancel')}</Button>
          <Button buttonType="primary" onClick={save}>
            {$t('button.save')}
          </Button>
        </div>
      }
    />
  );
}
