import React from 'react';
import { Button } from '../../components/button';
import { useNavigator } from '../../components/items-control';
import { useAboutLogo, useI18n } from '../../i18n';
import { SettingsViewName, SWITCH_NAME_SETTINGS } from './settings-view';
import { SettingsViewTemplate } from './settings-view-template';
import useSettingsAboutViewModel from '../../models/main-window/settings-about-view-model';
import { openUrl } from '../../java-api';
import { useContextmenuClickCombo } from '../../hooks/use-contextmenu-manager';

export default function SettingsAboutView() {
  const { $t } = useI18n();
  const settingsNavigator = useNavigator<SettingsViewName>(SWITCH_NAME_SETTINGS);
  const { copyrightYear, version } = useSettingsAboutViewModel();
  const logo = useAboutLogo();

  const contextmenuClickCombo = useContextmenuClickCombo();

  return (
    <SettingsViewTemplate
      header={$t('settings.about.title')}
      onHeaderClicked={() => settingsNavigator('general')}
      content={
        <div className="settings-view content-about">
          <img
            src={logo}
            onClick={contextmenuClickCombo.click}
            alt="logo"
            width="100px"
            height="104px"
            style={{ marginTop: '64px' }}
          />
          <span className="text tip-secondary" style={{ marginTop: '24px' }}>
            {$t('settings.about.currentVersion', version)}
          </span>
          <span className="text tip-secondary" style={{ marginTop: '16px' }}>
            {$t('settings.about.copyright', copyrightYear)}
          </span>
        </div>
      }
      footer={
        <div className="settings-view footer-about">
          <Button
            buttonType="link"
            onClick={() => openUrl('http://help.jianguoyun.com/?page_id=490')}
          >
            {$t('settings.about.userAgreement')}
          </Button>
          <Button buttonType="link" onClick={() => openUrl('http://help.jianguoyun.com/?p=3961')}>
            {$t('settings.about.privacyPolicy')}
          </Button>
        </div>
      }
    />
  );
}
