import React, { useMemo } from 'react';
import SyncStatePopup from './sync-state-popup';
import { openSandboxOwnerExpiredWindow, openSyncErrorsWindow, setIsPaused, SyncStatusType } from '../../java-api/main-window';
import { useI18n } from '../../i18n';
import useSyncStateBarModel from '../../models/main-window/sync-state-bar-model';

import statusErrorIcon from '../../assets/main-window/status_sync_error.svg';
import statusFinishIcon from '../../assets/main-window/status_sync_finish.svg';
import statusPausedIcon from '../../assets/main-window/status_sync_paused.svg';
import statusSyncingIcon from '../../assets/main-window/status_sync_syncing.svg';
import buttonPauseIcon from '../../assets/main-window/status_pause.svg';
import buttonPlayIcon from '../../assets/main-window/status_play.svg';
import { Tooltip } from '../../components/tooltip';

function getIcon(type: SyncStatusType) {
  switch (type) {
    case 'failed':
      return statusErrorIcon;
    case 'paused':
      return statusPausedIcon;
    case 'synced':
      return statusFinishIcon;
    case 'syncing':
      return statusSyncingIcon;
  }
}

export default function SyncStateBar() {
  const { $t } = useI18n();
  const { isPaused, type, details, popupData, syncErrorCount, sandboxOwnerExpired } = useSyncStateBarModel();
  const lockedBySelfFileItems =
    popupData?.type === 'locked-files'
      ? popupData.files.filter((item) => item.isLockedByThisMachine)
      : null;

  const popup = useMemo(() => {
    return popupData ? (
      <div className="sync-state-bar-popup">
        <SyncStatePopup data={popupData} />
      </div>
    ) : null;
  }, [popupData]);

  let extraInfo = null;
  if (type === 'synced') {
    if (lockedBySelfFileItems?.length) {
      extraInfo = (<span className="text tip-secondary" style={{ marginLeft: '8px' }}>
        {$t('syncStatus.extraInfo.lockedFileBySelf', lockedBySelfFileItems.length)}
      </span>);
    } else if (syncErrorCount > 0) {
      extraInfo = (<span className="text tip-secondary" style={{ marginLeft: '8px' }} onClick={async () => await openSyncErrorsWindow(false)}>
        {$t('syncStatus.extraInfo.hasSyncErrors', syncErrorCount)}
      </span>)
    }
  } else if (type === 'failed') {
    if (sandboxOwnerExpired) {
      extraInfo = (<span className="text tip-secondary" style={{ marginLeft: '8px' }} onClick={async () => await openSandboxOwnerExpiredWindow()}>
        {$t('syncStatus.extraInfo.clickForDetails')}
      </span>)
    }
  }

  return (    
    <div className="sync-state-bar-wrapper">
      <img className="sync-state-icon" src={getIcon(type)} alt="state-icon" />
      <span className="sync-state-message text ellipsis">
        {details}
        {extraInfo}
      </span>
      <button className="sync-state-button" onClick={() => setIsPaused(!isPaused)}>
        {isPaused ? (
          <Tooltip title={$t('syncStateBar.resume.tooltip')} placement='left'>
            <img src={buttonPlayIcon} alt="play-icon"></img>
          </Tooltip>
        ) : (
            <Tooltip title={$t('syncStateBar.pause.tooltip')} placement='left'>
              <img src={buttonPauseIcon} alt="pause-icon"></img>
            </Tooltip>
        )}
      </button>
      {popup}
    </div>
  );
}
