import React from 'react';
import { ScrollViewer } from '../../components/layout-container';
import { useI18n } from '../../i18n';
import { Button } from '../../components/button';
import { SearchResult } from '../../java-api/main-window';
import HighlightSpan from '../../components/highlight-span';
import useSyncFolderSearchResultViewModel from '../../models/main-window/sync-folder-search-result-view-model';
import EmptyView from './empty-view';
import { openPath } from '../../java-api/common-api';
import folderIcon from '../../assets/main-window/folder.svg';
import historyIcon from '../../assets/main-window/history.svg';
import closeIcon from '../../assets/main-window/close.svg';

type SearchTextHistoryViewProps = {
  history: string[];
  removeHistory: (index: number) => void;
  clearHistory: () => void;
  setSearchText: (value: string) => void;
};

type SearchResultViewProps = {
  searchResultItems: SearchResult[];
  queriesText: string;
  showDetails?: boolean;
};

type SyncFolderSearchResultViewProps = {
  searchText?: string;
  showDetails?: boolean;
  setSearchText: (value: string) => void;
};

function SearchHistoryView({
  history,
  removeHistory,
  clearHistory,
  setSearchText,
}: SearchTextHistoryViewProps) {
  const { $t } = useI18n();

  return history.length ? (
    <div className="search-history-view">
      <span className="text tip-secondary label-search-history">
        {$t('syncFolder.search.label.recentSearch')}
      </span>
      <Button className="button-clear-history" buttonType="link" onClick={clearHistory}>
        {$t('syncFolder.search.clearHistory')}
      </Button>
      <ul className="search-history-list">
        <ScrollViewer>
          {history.map((item, index) => (
            <li
              key={`${item}-${index}`}
              className="search-history-item"
              onClick={() => setSearchText(item)}
            >
              <img className="history-icon" src={historyIcon} alt="history" />
              <span className="text subtitle ellipsis link history-text">{item}</span>
              <button
                className="history-close-button"
                onClick={(e) => {
                  e.stopPropagation();
                  removeHistory(index);
                }}
              >
                <img src={closeIcon} alt="close" width="14px" height="14px" />
              </button>
            </li>
          ))}
        </ScrollViewer>
      </ul>
    </div>
  ) : (
    <EmptyView title={$t('syncFolder.search.emptyHistory')} />
  );
}

function SearchResultView({ searchResultItems, queriesText, showDetails}: SearchResultViewProps) {
  const { $t } = useI18n();
  const folderItems = searchResultItems.filter((item) => item.isDir);
  const fileItems = searchResultItems.filter((item) => !item.isDir);

  return (
    <ScrollViewer>
      {!showDetails ? ( 
        <span className="text label-search-tip">
          {$t('syncFolder.search.tip.more')}
        </span>
      ): null}
      {folderItems && folderItems.length > 0 ? (
        <>
          <span className="text tip-secondary label-group-title folder">
            {$t('syncFolder.search.label.folder', folderItems.length)}
          </span>
          <ul className="search-result-list folder">
            {folderItems.map(({ name, parentDir, fullPath }, index) => (
              <li
                key={`${name}-${index}`}
                className="search-result-item folder"
                onClick={() => openPath(fullPath)}
              >
                <img
                  className="folder-icon"
                  src={folderIcon}
                  alt="folder"
                  width="32px"
                  height="30px"
                />
                <HighlightSpan
                  className="text subtitle ellipsis link label-title"
                  sourceText={name}
                  queriesText={queriesText}
                />
                <HighlightSpan
                  className="text tip-secondary ellipsis label-subtitle"
                  sourceText={parentDir}
                  queriesText={queriesText}
                />
              </li>
            ))}
          </ul>
        </>
      ) : null}
      {fileItems && fileItems.length > 0 ? (
        <>
          <span className="text tip-secondary label-group-title file">
            {$t('syncFolder.search.label.file', fileItems.length)}
          </span>
          <ul className="search-result-list file">
            {fileItems.map(({ name, parentDir, fullPath }, index) => (
              <li
                key={`${name}-${index}`}
                className="search-result-item file"
                onClick={() => openPath(fullPath)}
              >
                <HighlightSpan
                  className="text subtitle ellipsis link label-title"
                  sourceText={name}
                  queriesText={queriesText}
                />
                <HighlightSpan
                  className="text tip-secondary ellipsis label-subtitle"
                  sourceText={parentDir}
                  queriesText={queriesText}
                />
              </li>
            ))}
          </ul>
        </>
      ) : null}
    </ScrollViewer>
  );
}

export default function SyncFolderSearchResultView({
  searchText,
  showDetails,
  setSearchText,
}: SyncFolderSearchResultViewProps) {
  const { $t } = useI18n();
  const {
    searchHistoryList,
    searchResultItems,
    removeHistory,
    clearHistory,
  } = useSyncFolderSearchResultViewModel(searchText, showDetails);

  return (
    <div className="sync-folder-search-result-wrapper">
      {!searchText ? (
        <SearchHistoryView
          history={searchHistoryList}
          removeHistory={removeHistory}
          clearHistory={clearHistory}
          setSearchText={setSearchText}
        />
      ) : searchResultItems && searchResultItems.length > 0 ? (
        <SearchResultView searchResultItems={searchResultItems} queriesText={searchText} showDetails={showDetails}/>
      ) : (
        <EmptyView
          title={$t('syncFolder.search.empty')}
          subtitle={$t('syncFolder.search.empty.description')}
        />
      )}
    </div>
  );
}
