import classNames from 'classnames';
import React from 'react';
import { ComboBox } from '../../components/items-control';
import { VirtualizingListBox } from '../../components/items-control';
import { openPath } from '../../java-api/common-api';
import { RecentEventItem } from '../../java-api/main-window/main-window-api';
import useRecentViewModel from '../../models/main-window/recent-view-model';

type RecentListBoxItemProps = {
  item: RecentEventItem;
};

function RecentListBoxItem({ item }: RecentListBoxItemProps) {
  const deleted = item.action === 'delete' || item.action === 'purge';
  const textMessageClass = classNames('text-message text ellipsis link', { deleted });

  return (
    <div className="recent-list-box-item-wrapper" onClick={() => openPath(item.filePath)}>
      <span className={textMessageClass}>{item.displayTitle}</span>
      <span className={`operation-tag ${item.action}`}>{item.displayAction}</span>
      <span className="text-editor">{item.editor}</span>
      <span className="text-time">{item.displayTime}</span>
    </div>
  );
}

export default function RecentView() {
  const {
    comboItemAllSandboxes,
    syncFolderItems,
    selectedItem,
    recentEventItems,
    setSelecetdItem,
  } = useRecentViewModel();

  return (
    <div className="recent-view-wrapper">
      <div className="search-box-region">
        <ComboBox
          itemsSource={[comboItemAllSandboxes, ...syncFolderItems]}
          selectedItem={selectedItem}
          setSelectedItem={setSelecetdItem}
          dorpDownBoxHeight="300px"
        />
      </div>
      <div className="recent-view-region">
        <VirtualizingListBox
          sizeProvider={{ itemSize: 68, itemCount: recentEventItems.length }}
          renderItems={(startIndex, endIndex) =>
            recentEventItems
              .slice(startIndex, endIndex)
              .map((item) => <RecentListBoxItem item={item} />)
          }
        />
      </div>
    </div>
  );
}
