import React from 'react';
import { Button } from '../../components/button';
import { ComboBox, useNavigator } from '../../components/items-control';
import { LanguageCode, useI18n } from '../../i18n';
import { openUrl, useIsOSXPlatform, useIsBadgeEnabled, useIsFileProviderEnabled, useShouldShowUpgradeToFileProviderEntry, isEnterprise} from '../../java-api';
import { fileProviderFeatureUpgrade } from '../../java-api/main-window';
import useSettingsGeneralViewModel from '../../models/main-window/settings-general-view-model';
import { SettingsViewName, SWITCH_NAME_SETTINGS } from './settings-view';
import {
  SettingItemSwitch,
  SettingsFooterButton,
  SettingsViewTemplate,
} from './settings-view-template';

function SettingsNavigatorFooter() {
  const { $t } = useI18n();
  const isOSXPlatform = useIsOSXPlatform();
  const isFileProviderEnabled = useIsFileProviderEnabled();
  const isEtp = isEnterprise();
  const settingsNavigator = useNavigator<SettingsViewName>(SWITCH_NAME_SETTINGS);

  return (
    <div className="settings-view footer-general">
      <div className="split-line"></div>
      <SettingsFooterButton
        text={$t('settings.network.title')}
        onClick={() => settingsNavigator('network')}
      />
      {/* Linux platform don't support cloud bridge */}
      {isOSXPlatform && !isFileProviderEnabled ? (
        <>
          <div className="split-line"></div>
          <SettingsFooterButton
            text={$t('settings.storage.title')}
            onClick={() => settingsNavigator('storage')}
          />
        </>
      ) : null}
      <div className="split-line"></div>
      <SettingsFooterButton
        text={$t('settings.about.title')}
        onClick={() => settingsNavigator('about')}
      />
      {/* [WS-168152] Hide customer support button for Enterprise edition*/}
      {isEtp ? null : (
        <>
          <div className="split-line"></div>
          <SettingsFooterButton
            text={$t('settings.customerService.title')}
            onClick={() =>
              openUrl('https://www.jianguoyun.com/s/helpdesk')
            }
          />
        </>
      )}
    </div>
  );
}

export default function SettingsGeneralView() {
  const { $t } = useI18n();
  const isOSXPlatform = useIsOSXPlatform();
  const isBadgeEnabled = useIsBadgeEnabled();
  const isFileProviderEnabled = useIsFileProviderEnabled();
  const shouldShowUpgradeToFileProviderEntry = useShouldShowUpgradeToFileProviderEntry();

  const {
    autoStart,
    notifyOnFilesUpdated,
    safeGuard,
    fileProtectionLock,
    showInDocker,
    experimental,
    languageItems,
    language,
    badgePreferenceItems,
    badgePreference,
  } = useSettingsGeneralViewModel();

  return (
    <SettingsViewTemplate
      header={$t('settings.general.title')}
      content={
        <div className="settings-view content-general">
          {shouldShowUpgradeToFileProviderEntry ? (
            // File Provider feature upgrade setting item
            <div className="setting-item-switch-wrapper">
              <div className="setting-feature-upgrade-red-dot" style={{marginTop: '6px', marginRight: '6px'}} />
              <div className="setting-item-content">
                <span className="text setting-item-title">{$t('settings.general.featureUpgrade.fileprovider.title')}</span>
                <span className="text setting-item-description">{$t('settings.general.featureUpgrade.fileprovider.description')}</span>
              </div>
              <div style={{alignSelf: 'flex-start'}}>
                <Button
                  buttonType='link'
                  onClick={() => fileProviderFeatureUpgrade()}>
                  {$t('settings.general.featureUpgrade.fileprovider.upgradeNow')}
                </Button>
              </div>
            </div>
          ) : null}
          <SettingItemSwitch
            title={$t('settings.general.autoStart.title')}
            checked={autoStart.value}
            setChecked={(v) => autoStart.setAndApply(v)}
          />
          <SettingItemSwitch
            title={$t('settings.general.notifyOnFilesUpdated.title')}
            checked={notifyOnFilesUpdated.value}
            setChecked={(v) => notifyOnFilesUpdated.setAndApply(v)}
          />
          <SettingItemSwitch
            title={$t('settings.general.safeGuard.title')}
            decription={$t('settings.general.safeGuard.description')}
            checked={safeGuard.value}
            setChecked={(v) => safeGuard.setAndApply(v)}
          />
          {isOSXPlatform && !isFileProviderEnabled ? (
            // The file protection lock only available on OSX
            <SettingItemSwitch
              title={$t('settings.general.fileProtectionLock.title')}
              decription={$t('settings.general.fileProtectionLock.description')}
              checked={fileProtectionLock.value}
              setChecked={(v) => fileProtectionLock.setAndApply(v)}
            />
          ) : null}
          {isOSXPlatform ? (
            <SettingItemSwitch
              title={$t('settings.general.showInDocker.title')}
              checked={showInDocker.value}
              setChecked={(v) => showInDocker.setAndApply(v)}
            />
          ) : null}
          <SettingItemSwitch
            title={$t('settings.general.experimental.title')}
            decription={$t('settings.general.experimental.description')}
            checked={experimental.value}
            setChecked={(v) => experimental.setAndApply(v)}
          />
          <div className="setting-item-combobox">
            <span className="text setting-item-title">{$t('settings.general.language.title')}</span>
            <ComboBox
              itemsSource={languageItems}
              selectedItem={languageItems.find((item) => item.value === language.value)!}
              setSelectedItem={(v) => language.setAndApply(v.value as LanguageCode)}
            />
          </div>
          {isBadgeEnabled ? (
            <div className="setting-item-combobox">
              <span className="text setting-item-title">{$t('settings.general.badge.title')}</span>
              <ComboBox style={{ minWidth: '200px' }}
                itemsSource={badgePreferenceItems}
                selectedItem={badgePreferenceItems.find((item) => item.value === badgePreference.value)!}
                setSelectedItem={(v) => badgePreference.setAndApply(v.value as number)}
              />
            </div>
          ) : null}
        </div>
      }
      footer={<SettingsNavigatorFooter />}
    />
  );
}
